"use client"

import { useEffect, useState } from "react"
import { useAuth } from "@/context/auth-context"
import { ProtectedLayout } from "@/components/layout/protected-layout"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Alert } from "@/components/ui/alert"

interface Sale {
  id: string
  pumpNumber: string
  fuelType: string
  amount: number
  quantity: number
  date: string
}

export default function SalesRecordsPage() {
  const { token } = useAuth()
  const [sales, setSales] = useState<Sale[]>([])
  const [filteredSales, setFilteredSales] = useState<Sale[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState("")
  const [filter, setFilter] = useState("all")

  useEffect(() => {
    fetchSales()
  }, [token])

  useEffect(() => {
    filterSales()
  }, [filter, sales])

  const fetchSales = async () => {
    try {
      const response = await fetch("http://localhost:5000/api/sales", {
        headers: { Authorization: `Bearer ${token}` },
      })
      const data = await response.json()
      setSales(data.data || [])
    } catch (err) {
      setError("Failed to fetch sales data")
    } finally {
      setLoading(false)
    }
  }

  const filterSales = () => {
    const now = new Date()
    let filtered = sales

    if (filter === "today") {
      filtered = sales.filter((s) => new Date(s.date).toDateString() === now.toDateString())
    } else if (filter === "week") {
      const weekAgo = new Date(now.getTime() - 7 * 24 * 60 * 60 * 1000)
      filtered = sales.filter((s) => new Date(s.date) >= weekAgo)
    } else if (filter === "month") {
      const monthAgo = new Date(now.getTime() - 30 * 24 * 60 * 60 * 1000)
      filtered = sales.filter((s) => new Date(s.date) >= monthAgo)
    }

    setFilteredSales(filtered)
  }

  const totalRevenue = filteredSales.reduce((sum, s) => sum + s.amount, 0)

  const handleExport = () => {
    const csv = [
      ["Pump", "Fuel Type", "Quantity (L)", "Amount ($)", "Date"],
      ...filteredSales.map((s) => [s.pumpNumber, s.fuelType, s.quantity, s.amount, s.date]),
    ]
    const csvContent = csv.map((row) => row.join(",")).join("\n")
    const blob = new Blob([csvContent], { type: "text/csv" })
    const url = window.URL.createObjectURL(blob)
    const a = document.createElement("a")
    a.href = url
    a.download = "sales-records.csv"
    a.click()
  }

  return (
    <ProtectedLayout>
      <div className="p-8">
        <h1 className="text-3xl font-bold text-slate-900 mb-8">Sales Records</h1>

        {error && (
          <Alert variant="destructive" className="mb-6">
            {error}
          </Alert>
        )}

        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
          <Card>
            <p className="text-sm text-slate-600">Total Revenue</p>
            <p className="text-3xl font-bold text-slate-900 mt-2">${totalRevenue.toFixed(2)}</p>
          </Card>
          <Card>
            <p className="text-sm text-slate-600">Total Transactions</p>
            <p className="text-3xl font-bold text-slate-900 mt-2">{filteredSales.length}</p>
          </Card>
          <Card>
            <p className="text-sm text-slate-600">Average Transaction</p>
            <p className="text-3xl font-bold text-slate-900 mt-2">
              ${(totalRevenue / filteredSales.length || 0).toFixed(2)}
            </p>
          </Card>
        </div>

        <div className="flex gap-4 mb-6">
          {["all", "today", "week", "month"].map((f) => (
            <Button
              key={f}
              variant={filter === f ? "primary" : "secondary"}
              onClick={() => setFilter(f)}
              aria-pressed={filter === f}
              aria-label={`Filter by ${f}`}
            >
              {f.charAt(0).toUpperCase() + f.slice(1)}
            </Button>
          ))}
          <Button variant="secondary" onClick={handleExport} aria-label="Export data">
            Export CSV
          </Button>
        </div>

        {loading ? (
          <p>Loading...</p>
        ) : (
          <Card>
            <div className="overflow-x-auto">
              <table className="w-full" role="table">
                <thead className="border-b border-slate-200">
                  <tr>
                    <th className="text-left py-3 px-4 font-semibold text-slate-900">Pump</th>
                    <th className="text-left py-3 px-4 font-semibold text-slate-900">Fuel Type</th>
                    <th className="text-left py-3 px-4 font-semibold text-slate-900">Quantity (L)</th>
                    <th className="text-left py-3 px-4 font-semibold text-slate-900">Amount</th>
                    <th className="text-left py-3 px-4 font-semibold text-slate-900">Date</th>
                  </tr>
                </thead>
                <tbody>
                  {filteredSales.map((sale) => (
                    <tr key={sale.id} className="border-b border-slate-100 hover:bg-slate-50">
                      <td className="py-3 px-4">{sale.pumpNumber}</td>
                      <td className="py-3 px-4">{sale.fuelType}</td>
                      <td className="py-3 px-4">{sale.quantity}</td>
                      <td className="py-3 px-4 font-semibold">${sale.amount.toFixed(2)}</td>
                      <td className="py-3 px-4">{new Date(sale.date).toLocaleDateString()}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </Card>
        )}
      </div>
    </ProtectedLayout>
  )
}
